import React, { useState,Fragment, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import ReadOnlyRow from "../components/ReadOnlyRow.js";
import EditableRow from "../components/EditableRow.js";

function PersonSearch() {
    const [keyword, setKeyword] = useState("");
    const [change, setChange] = useState(0);
    const [people, setPeople] = useState([]);
    const [editDataId, setEditDataId] = useState(null);
    const [data, setData] = useState();
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [post, setPost] = useState({
      name: "",
      email: "",
      gmail: "",
      is_duke: "",
      netid: "",
      unique_id: "",
      comments: "",
    });
  
    const navigate = useNavigate();

    const [editFormData, setEditFormData] = useState({
      name: "",
      email: "",
      gmail: "",
      is_duke: "",
      netid: "",
      unique_id: "",
      comments: "",
    })

    useEffect(() => {
      setLoading(true);
      axios.get("http://localhost:8000/api/person").then((response) => {
        setData(response.data);
        console.log(response.data);
      }).catch((err) => {
        setError(err);
      }).finally(() => {
        setLoading(false);
      })
    }, ["http://localhost:8000/api/person"]);
    
    if (loading) return <h1>LOADING...</h1>;
    if (error) console.log(error);

    const handleEditFormChange = (event) => {
      event.preventDefault();

      const fieldName = event.target.getAttribute("name");
      const fieldValue = event.target.value;

      const newFormData = { ...editFormData };
      newFormData[fieldName] = fieldValue;

      setEditFormData(newFormData);
      
      setPost({...post, [event.target.name]: event.target.value})
    }

    const handleEditClick = (event, data) => {
      event.preventDefault();
      setEditDataId(data.id);

      const formValues = {
        name: data.name,
        email: data.email,
        gmail: data.gmail,
        is_duke: String(data.is_duke),
        netid: data.netid,
        unique_id: data.unique_id,
        comments: data.comments,       
      }

      setEditFormData(formValues);
    }

     const SearchPersonInfo = () => {
        let formField = new FormData()
        console.log("in s");
        console.log(keyword);
        formField.append('keyword', keyword)
        axios.get('http://localhost:8000/api/search/?keyword=' + keyword).then((response) => {
        setPeople(response.data)
        setChange(1)
        console.log("get response");
        console.log(response.data);
        navigate('/person/')
        }).catch(error => {
        console.log(error)
        })
    }


    const handleEditFormSubmit = async (event) => {
      event.preventDefault();

      const editedData = {
        id: editDataId,
        name: editFormData.name,
        email: editFormData.email,
        gmail: editFormData.gmail,
        is_duke: editFormData.is_duke,
        netid: editFormData.netid,
        unique_id: editFormData.unique_id,
        comments: editFormData.comments,
      }
      
      axios({
        method: "put",
        url: "http://localhost:8000/api/person/" + editedData.id + "/",
        data: editedData
      }).then((response) => {
        console.log(response.data);
      }).catch(error => {
        console.log(error)
      })

      const newData = [...data];

      const index = data.findIndex((data) => data.id === editDataId);

      newData[index] = editedData;

      setData(newData);
      setEditDataId(null);

    }

    const inputStyle = {
      fontSize: '0.5rem', // Adjust the text size here
    };
    const inputStyleBar = {
      fontSize: '1rem', // Adjust the text size here
    };

    const tableStyle = {
      fontSize: '0.5rem', // Adjust the text size here
      tableLayout: "fixed"
    };

  var personDetails = "";
  personDetails = data?.map(data => {
    return (
      <Fragment>
        { editDataId === data.id ? (
        <EditableRow 
          editFormData={editFormData} 
          handleEditFormChange={handleEditFormChange} 
        />
      ) : (
        <ReadOnlyRow 
          data={data} 
          handleEditClick={handleEditClick}
        />
      )}  
      </Fragment>
    )
  });

  var searchDetails = "";
  searchDetails = people.map(person => {
    return (
      <Fragment>
        { editDataId === person.id ? (
        <EditableRow 
          editFormData={editFormData} 
          handleEditFormChange={handleEditFormChange} 
        />
      ) : (
        <ReadOnlyRow 
          data={person} 
          handleEditClick={handleEditClick}
        />
      )}  
      </Fragment>
    //     <tr key={person.id}>
    //     <td className='table-td'>{person.name}</td>
    //     <td className='table-td'>{person.email}</td>
    //     <td className='table-td'>{person.gmail}</td>
    //     <td className='table-td'>{String(person.is_duke)}</td>
    //     <td className='table-td'>{person.netid}</td>
    //     <td className='table-td'>{person.unique_id}</td>
    //     {/* change to facts */}
    //     {/* <td className='table-td'>{person.comments}</td> */}
    //     <td className='table-td'>{person.comments}</td>
    //     <td className='table-td'>
    //     <Link to="/" className="button-edit textsz">Edit</Link>
    //     </td>
    // </tr>)
    )
    });

    
    const title = () => {

        if(change){
            return searchDetails
        }else{
            return personDetails
        }
    }

    const CancelButton = () => {
        navigate('/person')
    }

  return (
    <div className="container">
        <div className = "search-container">
        <h4> Search people here</h4>
        <form>
            <label htmlFor="header-search"></label>
            <input
            type="text"
            id="header-search"
            placeholder = "Search by keywords"
            name="keyword"
            style={inputStyleBar} // Apply the inline styles
            onChange = {(e) => setKeyword(e.target.value)}
            />
            <button type="button" className="button-search" onClick={(e) => SearchPersonInfo(e)}> Search </button>
            <button type="submit" className = "button-cancel" > Cancel </button>
        </form>
        </div>

        <div className="table-container">
        <div className="row">
            <div className="col-md-12">
            <div className="card-header mb-3" >     
                <h4>People List</h4>
                <div>
                  <h4>
                    <Link to="/person/add" className="btn btn-primary float-end mb-2"> Add Student </Link>
                    <Link to="/person/csv" className="btn btn-primary float-end mb-2"> Upload CSV </Link>
                  </h4>
                </div>
            </div>
            <div className="card-body">
              <form onSubmit={handleEditFormSubmit}>
                <table
                  width="100%"
                  border={1}
                  cellPadding={2}
                  cellSpacing={1}
                  // style={tableStyle}
                  className="table table-striped "
                  > 
                  <thead>
                      <tr>
                      <th>Name</th>
                      <th>Email</th>
                      <th>Gmail</th>
                      <th>Is_duke</th>
                      <th>NetID</th>
                      <th>Unique ID</th>
                      {/* <th>Facts</th> */}
                      <th>Comments</th>
                      <th>Edit</th>
                      </tr>
                  </thead>
                  <tbody>
                      {title()}
                  </tbody>
                </table>
                </form>
            </div>
            </div>
        </div>
        </div>
    </div>
  )
}

export default PersonSearch;