import React from 'react';

const ReadOnlyRow = ({ data, handleEditClick }) => {

  return (
    <tr key={data.id}>
      <td className='table-td'>{data.name}</td>
      <td className='table-td'>{data.email}</td>
      <td className='table-td'>{data.gmail}</td>
      <td className='table-td'>{String(data.is_duke)}</td>
      <td className='table-td'>{data.netid}</td>
      <td className='table-td'>{data.unique_id}</td>
      <td className='table-td'>{data.comments}</td>
      <td className='table-td'>
        <button type="button" onClick={(event)=> handleEditClick(event, data)}>
          Edit
        </button>
      </td>
    </tr>
  );
};

export default ReadOnlyRow;