import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

// move new code here later
function AttributeTable({ attributeData, handleInputChange }) {
  return (
    <table>
      <thead>
        <tr>
          <th>Attribute</th>
          <th>Value</th>
        </tr>
      </thead>
      <tbody>
        {Object.keys(attributeData).map((key) => (
          <tr key={key}>
            <td>{key}</td>
            <td>
              <input
                type="text"
                name={key}
                value={attributeData[key]}
                onChange={handleInputChange}
              />
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}

// TODO
function handleUpload(selectedFile, columnMappings, courseName, overwriteConfirmation, setOverwriteConfirmation, setError) {
  if (selectedFile) {
    const formData = new FormData();
    formData.append("file", selectedFile);

    for (const key in columnMappings) {
      formData.append(key, columnMappings[key]);
    }

    formData.append("courseName", courseName);
    formData.append('overwriteConfirmation', overwriteConfirmation);

    try {
      axios.post("http://localhost:8000/api/upload_csv/", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
        .then((response) => {
          console.log("File uploaded successfully!");
          const { message, duplicate } = response.data;
          if (duplicate && duplicate === true) {
            console.log("handle overwrite");
            handleOverwriteConfirmation(selectedFile, columnMappings, courseName, overwriteConfirmation, setOverwriteConfirmation, setError);
          }
          window.location.reload();
        })
        .catch((error) => {
          console.error("File upload failed:", error);
          const errorMessage = error.response.data.message;
          setError(errorMessage);
        });
    } catch (error) {
      console.error("File upload failed:", error);
    }
  } else {
    console.log("No file selected.");
  }
}

function handleOverwriteConfirmation(selectedFile, columnMappings, courseName, overwriteConfirmation, setOverwriteConfirmation, setError) {
  const confirmed = window.confirm('The csvfile already exists. Do you want to overwrite it?');
  if (confirmed) {
    setOverwriteConfirmation(true);
    handleUpload(selectedFile, columnMappings, courseName, true, setOverwriteConfirmation, setError);
  }
}

function CSVButton({ selectedFile, attributeData, courseName, overwriteConfirmation, setOverwriteConfirmation, setError }) {
  return (
    <div className="container mb-5">
      <button className="form-control" onClick={() => handleUpload(selectedFile, attributeData, courseName, overwriteConfirmation, setOverwriteConfirmation, setError)}>
        upload csv
      </button>
    </div>
  );
}

const UpLoadCSV = () => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [attributeData, setAttributeData] = useState({
    name: "",
    email: "",
    gmail: "",
    netid: "",
    unique_id: "",
    grade: "",
  });

  const [courseName, setCourseName] = useState('');
  const [fileContent, setFileContent] = useState('');
  const [overwriteConfirmation, setOverwriteConfirmation] = useState(false);
  const [error, setError] = useState('')

  function handleFileUpload(event) {
    const file = event.target.files[0];
    setSelectedFile(file);

    const reader = new FileReader();
    reader.onload = () => {
      const content = reader.result;
      setFileContent(content);
      const fileName = file.name.split('.')[0];
      setCourseName(fileName);
    };
    reader.readAsText(file);
  }

  const handleColumnMappingChange = (attribute, column) => {
    setAttributeData((prevData) => ({
      ...prevData,
      [attribute]: column,
    }));
  };

  const handleCourseNameChange = (event) => {
    setCourseName(event.target.value);
  };

  return (
    <div className="container">
      {/* <div className = "container"> */}
      <div className="CSV-page">
        <h2 className="text-center"> Upload your CSV file here</h2>
        <br></br>
        <div className="App">
          <div className="card-body">
            <input className="form-control" type="file" onChange={handleFileUpload} />
            <br></br>
            {/* <AttributeTable attributeData={attributeData} handleInputChange={handleInputChange} /> */}

            <br></br>

            {fileContent && (
              <div>
                {/* <h3>Column Mapping:</h3> */}
                <div className="input-group">
                  <div className="input-group-prepend">
                    <span className="input-group-text">Course Name:</span>
                  </div>
                  <input
                    type="text"
                    value={courseName}
                    onChange={handleCourseNameChange}
                    placeholder="Enter course name"
                    className="form-control"
                  />
                </div>
                <br></br>
                <table>
                  <thead>
                    <tr>
                      <th>Attribute</th>
                      <th>CSV Column</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Object.keys(attributeData).map((attribute) => (
                      <tr key={attribute}>
                        <td>{attribute}</td>
                        <td>
                          <select
                            value={attributeData[attribute] || ''}
                            onChange={(e) => handleColumnMappingChange(attribute, e.target.value)}
                          >
                            <option value="">Ignore</option>
                            {fileContent.split('\n')[0].split(',').map((column, index) => (
                              <option key={index} value={column}>
                                {column}
                              </option>
                            ))}
                          </select>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
            <br></br>
            <CSVButton selectedFile={selectedFile} attributeData={attributeData} courseName={courseName} overwriteConfirmation={overwriteConfirmation} setOverwriteConfirmation={setOverwriteConfirmation} setError={setError} />
            <br></br>
            {error && <p>{error}</p>}
          </div>
        </div>
      </div>
    </div>
  )
}

export default UpLoadCSV
